import { Component, OnInit, OnDestroy } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {Subscription} from "rxjs/Rx";
import { RestaurantService } from '../restaurants/restaurant.service';
import { SaucerService } from '../saucers/saucer.service';

@Component({
  selector: 'app-saucers',
  templateUrl: './saucers.component.html',
  styleUrls: ['./saucers.component.css'],
  providers: [SaucerService, RestaurantService]
})
export class SaucersComponent implements OnInit, OnDestroy {
	
  //subscription :Subscription = null;
  restaurant  = {};
  restaurantId : string;
  saucers = [];
  subscription : Subscription;

  constructor(private route: ActivatedRoute,private saucerService: SaucerService,private restaurantService: RestaurantService ) { }

  ngOnInit() {

  	  	
  	this.subscription = this.route.params.subscribe(
	  		(params:any) =>{
          this.restaurantId = params['id'];
	  			this.restaurantService.getRestaurant(this.restaurantId).then(response =>{
			  		this.restaurant = response;
			  		console.log(this.restaurant);

            this.saucerService.getSaucers(response.id).then(response =>{
          this.saucers = response;
          console.log(this.saucers);

			  	});
          
	  		}
  		);
  		

  	  	
	  	});



  }

  ngOnDestroy() {
  	
  	this.subscription.unsubscribe();
  	
  }

}



