import { Component, OnInit, OnDestroy } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {Subscription} from "rxjs/Rx";
import { CommentService } from '../comments/comment.service';
import { SaucerService } from '../saucers/saucer.service';
import { RestaurantService } from '../restaurants/restaurant.service';

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css'],
  providers: [ CommentService, SaucerService, RestaurantService ]
})
export class CommentsComponent implements OnInit, OnDestroy {

  comments = [];
  saucer = [];
  restaurant = [];
  subscription : Subscription;
  saucerId : string;
  data = {};
  commentSucccess = false;
  commentError = false;

  constructor(private route: ActivatedRoute, private commentService: CommentService, private saucerService: SaucerService, private restaurantService: RestaurantService) { }

  ngOnInit() {

  	  	this.subscription = this.route.params.subscribe(
	  		(params:any) =>{

          this.restaurantService.getRestaurant(params['restaurantId']).then(response =>{
            this.restaurant = response;
            console.log(this.restaurant);

          });

          this.saucerId = params['saucerId'];
	  			this.commentService.getComments(this.saucerId).then(response =>{
			  		this.comments = response;
			  		console.log(this.comments);

			  	});

			  	this.saucerService.getSaucer(this.saucerId).then(response =>{
			  		this.saucer = response;
			  		console.log(this.saucer);

			  	});
          
  		

  	  	
	  	});

  }

   ngOnDestroy() {
  	
  	this.subscription.unsubscribe();
  	
  }


  sendComment(e){
    this.commentService.sendComment(this.saucerId, this.data)
    .then(response =>{
      this.comments.push(response);
      this.data = {};
      this.commentSucccess = true;
    })
    .catch(response =>{
          this.commentSucccess = false;
          this.commentError = true;
        }
      )
  }

}



