import { Injectable } from '@angular/core';
import { Headers, Http, Response } from "@angular/http";
import  'rxjs/Rx';
import { environment } from '../../environments/environment';

@Injectable()
export class CommentService{

	//private apiUrl= 'https://stark-river-41252.herokuapp.com/api/';
	
	constructor(private http: Http) {
		
	}

	getComments(saucerId: string){
		return this.http.get(environment.API_URL + 'saucers/' + saucerId + '/comments')
				.map((response: Response) => response.json())
				.toPromise();
	}

	sendComment(saucerId: string, data){
		data.date = new Date().toUTCString();

		let body = JSON.stringify(data);

		return this.http.post(environment.API_URL + 'saucers/'+ saucerId + '/comments', data)
		.map((response: Response) => response.json())
		.toPromise();
	}

}



