import { Injectable } from '@angular/core';
import { Headers, Http, Response } from "@angular/http";
import  'rxjs/Rx';
import { environment } from '../../environments/environment';

@Injectable()
export class SaucerService{

	//apiUrl= 'https://stark-river-41252.herokuapp.com/api/';
	
	constructor(private http: Http) {
		
	}

	
	getSaucers(restaurantId: string){
		return this.http.get(environment.API_URL +'restaurants/'+ restaurantId + '/saucers')
				.map((response: Response) => response.json())
				.toPromise();
	}

	getSaucer(saucerId: string){
		return this.http.get(environment.API_URL +'saucers/'+ saucerId )
				.map((response: Response) => response.json())
				.toPromise();
	}
}



