import { Injectable } from '@angular/core';
import { Headers, Http, Response } from "@angular/http";
import  'rxjs/Rx';
import { environment } from '../../environments/environment';

@Injectable()
export class RestaurantService{

	//apiUrl= 'https://stark-river-41252.herokuapp.com/api/';
	//restaurants = ['Chai', 'McDonalds', 'Fonda del Queso'];
	
	constructor(private http: Http) {
		
	}

	getRestaurants(){ 
		//return [{name: "Chai", description: ".. C"},{name: "McDonalds", description: ".. M"}];
		return this.http.get(environment.API_URL + 'restaurants')
				.map((response: Response) => response.json())
				.toPromise();
				
	}
	
	getRestaurant(id: string){
		return this.http.get(environment.API_URL + 'restaurants/'+id)
				.map((response: Response) => response.json())
				.toPromise();
	}
}



